












































import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {},
})
export default class Banner extends Vue {   
    get colorTheme(){
        return this.$store.getters.getColorTheme;
    }   
    
    private socialLinks: any = [
        {
            title:'Linkedin',
            link:'http://linkedin.com/in/srivind',
            icon:'mdi-linkedin'
        },
        {
            title:'Github',
            link:'http://github.com/srivind',
            icon:'mdi-github'
        },
        {
            title:'Gmail',
            link:'https://mail.google.com/mail/?view=cm&fs=1&to=srivindv@gmail.com',
            icon:'mdi-gmail'
        },
        {
            title:'Linkedin',
            link:'http://instagram.com/srivind',
            icon:'mdi-instagram'
        },
        {
            title:'Linkedin',
            link:'http://facebook.com/srivind',
            icon:'mdi-facebook'
        }
    ]

    private profileImg: any = require('@/assets/photo.jpg');
    private constants: any = require('../constants/index.json');


    redirectUrl(url: string): void{
        if(url.includes('mail')){
            if (/Mobi/.test(navigator.userAgent)) {
                const mailtoLink = "mailto:" + 'srivindv@gmail.com' +
                "?subject=" + encodeURIComponent('') +
                "&body=" + encodeURIComponent('');
                window.location.href = mailtoLink;
            } else{
                window.open(`https://mail.google.com/mail/?view=cm&fs=1&to=srivindv@gmail.com`, '_blank');
            }
        }else{
            window.open(url, '_blank');
        }
    }

    downloadResume(){
        window.open(this.constants.resume, '_self');
        
    }
}
